import React from 'react'
import Tabla from './Tabla';
import AgregarPartido from './AgregarPartido';
import Menu from './Menu'
import PromedioGolesTodos from './PromedioGolesTodos'
import AgregarEquipo from './AgregarEquipo';
import Goleadores from './Goleadores';
import { Container, Col, Row } from 'react-bootstrap';
import { connect } from 'react-redux';
import { useHistory} from "react-router-dom";

const Dashboard = (props) => {

    let { equipoUsuario } = props

    let history = useHistory()

    if (equipoUsuario==="")
        history.push("/")

    return (
        <div>
            <Menu />
            <Container style={{ paddingTop: "20px" }}>
                <Row>
                    <Col lg={7}>
                        <Tabla />
                    </Col>


                    <Col md={12} lg={5}>
                        <AgregarPartido />
                        <AgregarEquipo />
                    </Col>
                </Row>
                <Row>
                    <Col lg={6}>
                        <PromedioGolesTodos />
                    </Col>
                    <Col lg={6}>
                        <Goleadores />
                    </Col>

                </Row>
            </Container>
        </div>
    )
}

const mapStateToProps = (state) => ({
    equipoUsuario: state.equipoUsuario
})
export default connect(mapStateToProps)(Dashboard)

